.container{
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-bottom: 20px;
  margin-top: 30px;
}

.card{
  height: 350px; 
  width: 350px;
  background: #DCDCDC;
}

.MuiCard-root {
  overflow: hidden;
}

.pie{
  height: 350px; 
  width: 350px;
}

.title{
  width: 100%;
  align-content: center;
  font-size: 14px;
  margin-bottom:10px;
  margin-top: 6px;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
}

