.body {
  background-size:cover;
  background-repeat:no-repeat;

  }
 
.formcontainer {
    width: 450px;
    height: 550px;
    background-color:white;
    box-shadow: 0 0 20px black;
    border-radius: 8px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 98px;
}

.img1 {
    width: 450px;
    height: 250px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    background: transparent;

}

.text {
    font-size: 25px;
    font-family: 'Roboto Slab', serif;
    text-align: center;
    position: relative;
    top: -20px
}
.email,.password {
    width: 299px;
    height: 1px;
    position: relative;
    top: -30px;
    left: 12%;
    border-radius: 5px;
    border-width: 1px;
    border-color: gray;
    padding: 20px;
    font-size: 20px;
    outline: none;
    margin-bottom: 15px
}
.email:hover,.password:hover {
    background-color: gainsboro
}
.email:focus,.password:focus {
    box-shadow: 0 0 10px green;
    border: none
}
.check {
    position: relative;
    top: 28px;
    left: -66%
}
.text1 {
    font-size: 17px;
    font-family: 'Roboto Slab', serif;
    position: relative;
    top: -36px;
    left: 17%;
    color: gray
}
.forget {
    position: relative;
    top: -77px;
    left: 67%;
    color: darkblue;
    font-size: 14px;
    font-family: 'Roboto Slab', serif
}
.login {
    width: 350px;
    height: 40px;
    position: relative;
    left: 12%;
    top: -33px;
    cursor: pointer;
    font-size: 22px;
    font-family: 'Roboto Slab', serif;
    border-radius: 5px;
    border: none;
    background-color: #008000;
    color: white;
    margin-top: 43px;
}

.logo{
  position:fixed; 
  top:15px;
  left:20px;
  height:76px
}
.login:hover {
    background-color: #008000;
    opacity: 0.7;
}
.text2 {
    font-size: 16px;
    font-family: 'Roboto Slab', serif;
    position: relative;
    top: -70px;
    left: 40%;
    color: gray
}


@media only screen and (max-width:830px) {
    #formcontainer {
        position: relative;
        left: 20%
    }
}

@media only screen and (max-width:620px) {
    #formcontainer {
        position: relative;
        left: 10%
    }
}

@media only screen and (max-width:530px) {
    #formcontainer {
        position: relative;
        left: 4%
    }
}

.center{
  align-content: center;
}