
  
  .ul1 {
    padding-inline-start: 0px;
 }
  
  
  body{
    background: white;
  }
  
  .wrapper{
    margin-left:10px;
    margin-right:10px;
    position: absolute;
    z-index: 1;
  }
  
  .wrapper .top_navbar{
    width: calc(100% - 20px);
    height: 60px;
    display: flex;
    position: fixed;
    background-color:white;
   
  }

  .wrapper .top_navbar .top_menu .logo{
    margin-left : 0px;
  }

  .logo_top{
    margin-top:13px;
  }
  
  .wrapper .top_navbar .hamburger{
    width: 70px;
    height: 100%;
    background: #2e4ead;
    padding: 15px 17px;
    border-top-left-radius: 20px;
    cursor: pointer;
    background:black;
  }
  
  .wrapper .top_navbar .hamburger div{
    width: 35px;
    height: 4px;
    background: #92a6e2;
    margin: 5px 0;
    border-radius: 5px;
  }
  
  .wrapper .top_navbar .top_menu{
    width: calc(100% - 30px);
    height: 100%;
    background: black;
    border-top-right-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
  }
  
  .wrapper .sidebar{
    position: fixed;
    top: 60px;
    left: 10px;
    background: black;
    width: 175px;
    height: calc(100% - 60px);
    border-bottom-left-radius: 20px;
    transition: all 0.3s ease;
  }
  
  .wrapper .sidebar ul li a{
      display: block;
      padding: 20px;
      color: #fff;
      position: relative;
      margin-bottom: 1px;
      color: #92a6e2;
      white-space: nowrap;
  }
  
  .wrapper .sidebar ul li a:before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: #92a6e2;
    display: none;
  }
  
  .wrapper .sidebar ul li a:hover{
    background: #4360b5;
    color: #fff;
    opacity: 0.7;
  }
  .wrapper .sidebar ul li a.active{
    background: #4360b5;
    color: #fff;
  }
  
  .wrapper .sidebar ul li a:hover:before,
  .wrapper .sidebar ul li a.active:before{
    display: block;
  }
  
  .wrapper .main_container{
    width: calc(100%-200px);
    margin-top: 70px;
    margin-left: 200px;
    padding: 15px;
    transition: all 0.3s ease;
  }
  


.subItem{
  margin-left: 19px;
  margin-top: 13px;
  color: white;
  font-size: 16px;
}

.subItem:hover{
  background: #4360b5;
  color: #fff;
  opacity: 0.7;
 }
  .subItem.active{
    background: #4360b5;
    color: #fff;
  }


  .active{
    background: #4360b5;
    color: #fff;
  }