.container{
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-bottom: 20px;
  margin-top: 30px;
}

.card{
  height: 420px; 
  width: 350px;
  background: #DCDCDC;
}

.MuiCard-root {
  overflow: hidden;
}

.title{
  width: 100%;
  align-content: center;
  font-size: 14px;
  margin-bottom:10px;
  margin-top: 6px;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
}

.sub-title{
  font-size: 13px;
  margin-top: 7px;
}

.title1{
  width: 100%;
  font-size: 14px;
  margin-bottom: 10px;
  margin-top: 14px;
  font-size: 19px;
  font-weight: bold;
  text-align: left;
}

.subcontainer{
  padding-top: 80px;
  padding-left: 210px;
  margin-left: 27px;
  
}

.heading{
  display: block;
  font-size: 1.5em;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

