.container{
  padding-top: 76px;
  padding-left: 210px;
  margin-left: 27px;
  
}
.sub-container{
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-bottom: 20px;
  margin-top: 30px;
}
.sub-title{
  font-size: 13px;
  margin-top: 7px;
}
.main-heading{
    display: block;
    font-size: 1.5em;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}
.title{
  font-size: 19px;
  margin-top: 7px;
  width: 100%;
}

.title1{
  font-size: 16px;
  margin-top: 7px;
  width: 100%;
  margin-left: 71px;
}

.pie{
  height: 350px; 
  width: 350px;
}