.header-container{
    //@include display-flex-center;
    width: 100%;
    height: 46px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    z-index: 2;
    position: relative;
    top: 0;
    left: 0;
}

.table-header-container{
    width: 100%;
    text-transform: uppercase;
    // @include display-flex-start
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    box-sizing: border-box;
    padding-top: 10px;
}
.user{
    padding-bottom: 5px;
}
.position-header{
    padding-left: 43px;
}
.position-body{
    //@include display-flex-center;
}
.table-body-container{
    height: calc(100% - 25px);
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden;
}
.table-row{
    //@include display-flex-horizontal-start;
    padding: 12px 0;
}
.seperation-line{
    width: 826px;
    box-sizing: border-box;
    margin: 0 36px;
    border: 1px solid #407EA6;
    opacity: 0.1;
}
.table-container{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    margin-top: 47px;
}
.user{
    min-width: 355px;
    box-sizing: border-box;
    padding-left: 6px;
}
.headerTitle{
    font-size: '12px';
    font-weight: 600;
    font-style: 'normal';
    text-transform: 'uppercase';
}

